img {
  max-width: 100%;
  height: auto;

  .hentry & {
    @extend .z-depth-1;
  }

  &.alignleft {
    float: left;
  }

  &.alignright {
    float: right;
    max-width: 50%;

    @media #{$large-and-up} {
      margin: 0 -33% 20px 20px;
      max-width: 75%;
    }
  }
}



